import React from "react";
import "./Item.css";
import cartImg from "./Cart.svg";

const Item = ({ item }) => {
  const { id, name, price, amount } = item;
  return (
    <div className="Item" item-id={id}>
      <img className="Item-img" alt={name} src={cartImg} />
      <div className="Item-text-wrap">
        <div className="Item-name">{name}</div>
        <div className="Item-price">₩ {Number(price).toLocaleString()}</div>
      </div>
      <div className="Item-etc">
        <div className="Item-up-btn Item-btn">▲</div>
        <div className="Item-amount">{amount}</div>
        <div className="Item-down-btn Item-btn">▼</div>
        <div className="Item-delete-btn Item-btn">&times;</div>
      </div>
    </div>
  );
};

export default Item;
